import './Users.css';
import { NavLink } from 'react-router-dom';
import profile_default_pic from '../../imgs/profile_default_pic.png'

let User = (props) => {
  
  return (
    <div className="users_item" key={props.user.key}>
      <div className="user_profile">
        <NavLink to={`/profile/${props.user.id}`}>
          <div className="users_img">
            {/* <img src={props.img} alt="" /> */}
            <img src={props.user.photos.small || profile_default_pic} alt="" />
          </div>
        </NavLink>
        {props.user.followed ?
          <div className="user_profile__btn active" onClick={() => { props.unfollowUser(props.user.id) }}><button disabled={props.isFollowingInProgress.some(id => id === props.user.id)}>unfollow</button></div> :
          <div className="user_profile__btn" onClick={() => { props.followUser(props.user.id) }}><button disabled={props.isFollowingInProgress.some(id => id === props.user.id)}>follow</button></div>}
      </div>
      <div className="user_content">
        <NavLink to={`/profile/${props.user.id}`}>
          <div className="user_name">{props.user.name}</div>
        </NavLink>
        <div className="user_status">{props.user.status}</div>
      </div>
      <div className="user_location">
        {/* <div className="">{props.location.country}</div>
        <div className="">{props.location.city}</div> */}
      </div>
    </div>
  )
}
export default User
